.first {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  min-height: 900px;

  &.fixed {
    padding-top: 90px;
  }
}

.first__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  overflow: hidden;

  & video {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    min-height: 100%;
  }
}

@media (max-width: 991px) {
  .first__bg video {
    transform: translate(-30%, -0);
  }
}
@media (max-width: 575px) {
  .first {
    min-height: 740px;
    background-color: rgba(27, 117, 188, 0.7);
  }
  .first__bg {
    display: none;
  }
}
