* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #FFF;
  font-family: "Helvetica", Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;
  max-width: 850px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 40px;
}

:root {
  --color-dark: #0c2742;
  --color-blue: #1b75bc;
  --color-light-blue: #eef2f5;
  --color-white: #fff;
  --color-yellow: #f9ee8e;
  --color-green: #02ad23;
}

* {
  padding: 0;
  margin: 0;
}

body.lock {
  overflow: hidden;
}

.header {
  height: 90px;
  font-family: "Oswald", sans-serif;
  transition: all 0.2s linear;
}
.header .container {
  max-width: 1400px;
}

.fixed .header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  background-color: var(--color-white);
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.svg-image-header-logo,
.svg-image-header-logo-black {
  width: 174px;
  height: auto;
}

.svg-image-header-logo-black {
  display: none;
}

.fixed .header .svg-image-header-logo-black {
  display: block;
}
.fixed .header .svg-image-header-logo {
  display: none;
}
.fixed .header .header__link {
  color: var(--color-black);
}
.fixed .header .header__link.active.active {
  color: var(--color-white);
}
.fixed .header .header__appply {
  color: var(--color-white);
  background-color: var(--color-blue);
}

.header__navbar {
  height: 90px;
}
.header__navbar .header__list {
  height: 100%;
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.header__link {
  height: 100%;
  padding: 28px 11px;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  color: var(--color-white);
}
.header__link:last-child {
  margin: 0;
}
.header__link.active {
  color: var(--color-white);
  background-color: var(--color-blue);
}
.header__link a {
  display: block;
  padding: 5px 10px;
  cursor: pointer;
}

.header__appply {
  padding: 0;
  height: 56px;
  color: var(--color-blue);
  background-color: var(--color-white);
  border-radius: 3px;
}
.header__appply a {
  display: block;
  padding: 16px 24px;
}

.menu__burger {
  display: none;
  position: relative;
  width: 21px;
  height: 16px;
  cursor: pointer;
  z-index: 150;
  position: fixed;
  top: 28px;
  right: 35px;
  z-index: 12000;
}

.menu__burger span {
  position: absolute;
  background-color: var(--color-white);
  left: 0;
  width: 100%;
  height: 2px;
  top: 7px;
  border-radius: 100px;
  transition: all 0.2s linear;
}

.menu__burger:before,
.menu__burger:after {
  content: "";
  background-color: var(--color-white);
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  border-radius: 100px;
  transition: transform 0.2s linear;
}

.fixed .menu__burger span,
.fixed .menu__burger:before,
.fixed .menu__burger:after {
  background-color: var(--color-dark);
}
.fixed .menu__burger.active span,
.fixed .menu__burger.active:before,
.fixed .menu__burger.active:after {
  background-color: var(--color-white);
}

.menu__burger:before {
  top: 0;
}

.menu__burger:after {
  bottom: 0;
}

.menu__burger.active:before {
  transform: rotate(45deg);
  top: 7px;
}

.menu__burger.active:after {
  transform: rotate(-45deg);
  bottom: 7px;
}

.menu__burger.active span {
  transform: scale(0);
}

.burger__navbar {
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: -100%;
  z-index: 11000;
  width: 70%;
  height: 100%;
  padding: 20px;
  background-color: var(--color-white);
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  transition: all 0.3s linear;
}

.burger__overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s linear;
}

.burger__list {
  width: 50%;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.burger__link {
  padding: 16px 0;
  font-size: 20px;
  cursor: pointer;
}

.burger__appply {
  padding: 0;
  height: 56px;
  color: var(--color-white);
  background-color: var(--color-blue);
  border-radius: 3px;
  cursor: pointer;
}
.burger__appply a {
  display: block;
  padding: 16px 24px;
}

@media (max-width: 991px) {
  .header {
    height: 60px;
    padding-top: 8px;
  }

  .svg-image-header-logo,
.svg-image-header-logo-black {
    width: 144px;
    height: auto;
  }

  .header__navbar {
    display: none;
  }

  .menu__burger {
    display: block;
  }

  .burger__navbar.active {
    transform: translateX(142%);
  }

  .burger__overlay.active {
    visibility: visible;
    opacity: 1;
  }
}
@media (max-width: 767px) {
  .burger__navbar {
    display: block;
  }

  .burger__list {
    width: 100%;
    margin-bottom: 20px;
  }

  .burger__appply {
    width: 150px;
    text-align: center;
  }
}
.first {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  min-height: 900px;
}
.first.fixed {
  padding-top: 90px;
}

.first__bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  overflow: hidden;
}
.first__bg video {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
}

@media (max-width: 991px) {
  .first__bg video {
    transform: translate(-30%, 0);
  }
}
@media (max-width: 575px) {
  .first {
    min-height: 740px;
    background-color: rgba(27, 117, 188, 0.7);
  }

  .first__bg {
    display: none;
  }
}
.intro {
  flex-grow: 1;
}
.intro .container {
  max-width: 1328px;
}

.intro__wrapper {
  min-height: calc(100vh - 90px);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 170px 0 100px 27px;
}

.intro__title {
  font-family: "Oswald", sans-serif;
  margin: 0;
  font-weight: 400;
  font-size: 120px;
  line-height: 100px;
  letter-spacing: 0.4px;
  color: var(--color-white);
  text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}
.intro__title p {
  margin: 0;
}
.intro__title .intro__static {
  margin-bottom: 24px;
}
.intro__title .intro__frame {
  height: 120px;
  overflow: hidden;
}
.intro__title .intro__dynamic {
  color: var(--color-yellow);
  transform: translateY(120px);
  opacity: 0;
  animation: TextSlider 10s linear 0.5s infinite;
  animation-fill-mode: forwards;
}
.intro__title .intro__dynamic p {
  margin-bottom: 20px;
}
.intro__title .intro__dynamic p:last-child {
  margin-bottom: 0;
}

.intro__info {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.intro__about {
  max-width: 596px;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: 0.4px;
  color: rgba(255, 255, 255, 0.8);
}

.intro__scroll {
  display: flex;
  align-items: center;
  transform: rotate(90deg);
  animation: Rotate 0.5s linear 0.5s;
  animation-fill-mode: forwards;
  opacity: 0;
}
.intro__scroll p {
  margin-left: 12px;
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 38px;
  letter-spacing: 0.4px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.8);
}

@keyframes Rotate {
  0% {
    transform: rotate 90deg;
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: rotate(0);
    opacity: 1;
  }
}
@keyframes TextSlider {
  0% {
    opacity: 0;
    transform: translateY(120px);
  }
  2% {
    opacity: 1;
  }
  8% {
    transform: translateY(0);
  }
  16% {
    transform: translateY(0);
  }
  24% {
    transform: translateY(-120px);
  }
  32% {
    transform: translateY(-120px);
  }
  40% {
    transform: translateY(-240px);
  }
  98% {
    opacity: 1;
  }
  100% {
    transform: translateY(-240px);
    opacity: 0;
  }
}
@media (max-width: 991px) {
  .intro__title {
    font-size: 80px;
    line-height: 92px;
  }

  .intro__title .intro__static {
    margin-bottom: 10px;
  }

  .intro__about {
    max-width: 400px;
  }
}
@media (max-width: 767px) {
  .intro__about {
    margin: 0;
    max-width: 320px;
    font-size: 24px;
  }

  .svg-image-arrow-down {
    width: 28px;
  }
}
@media (max-width: 575px) {
  .intro .container {
    width: 100%;
    padding: 10px;
  }

  .intro__wrapper {
    padding: 0;
  }

  .intro__info {
    display: block;
  }

  .intro__about {
    width: 100%;
    margin-bottom: 50px;
    font-size: 20px;
  }

  .intro__title {
    font-size: 50px;
  }
  .intro__title .intro__static {
    margin: 0;
  }
}
.main {
  overflow: hidden;
}
.main .container {
  max-width: 1660px;
  padding: 0 30px;
}

body {
  overflow-x: hidden;
  background-color: var(--color-light-blue);
}

.about {
  position: relative;
}

.about__container {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: flex-end;
  max-width: 1660px;
  padding: 0 30px;
}

.about__wrapper {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  display: grid;
  align-items: flex-end;
  grid-template-areas: "title info" "description info";
  grid-template-columns: 41.66% 48.89%;
  gap: 64px 9.45%;
  padding: 108px 0 238px 12.68%;
}

.about__title {
  justify-self: end;
  max-width: 482px;
  grid-area: title;
  margin-left: 20.75%;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 90px;
  line-height: 100px;
  letter-spacing: 0.4px;
  color: var(--color-dark);
}
.about__title span {
  color: var(--color-blue);
}

.about__description {
  max-width: 408px;
  grid-area: description;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: 0.2px;
  color: var(--color-dark);
}

.about__info {
  grid-area: info;
  padding-top: 164px;
  justify-self: end;
}

.about__img img {
  display: block;
  width: 100%;
  height: auto;
}

.about__more {
  position: relative;
}

.about__square {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 174px;
  height: 174px;
  background-color: var(--color-blue);
  border: 1px solid #fff;
  transform: rotate(45deg) translate(-123px, 0px);
  filter: drop-shadow(4px 4px 40px rgba(27, 117, 188, 0.4));
}

.about__text {
  position: absolute;
  top: -30px;
  left: -38px;
